<style scoped>
  div {
    line-height: 24px;
  }
  p {
    margin-bottom: 10px;
  }
</style>

<template>
  <div>
    <p>Javascript变量作用域(全局变量,局部变量)。</p>
    <p>函数内部可以直接读取全局变量,函数外部自然无法读取函数内的局部变量</p>
    <p>函数内部声明变量的时候,要用var。如果不用,实际上声明了一个全局变量(声明提前)</p>

    <h2 class="color-green mt-20">什么是闭包</h2>
    <p>
      <span class="color-green">官方解释: </span>
      一个拥有许多变量和绑定了这些变量的环境的表达式（通常是一个函数）,因而这些变量也是该表达式的一部分
    </p>
    <p>
      <span class="color-green">理解: </span>
      Javascript允许使用内部函数---即函数定义和函数表达式位于另一个函数的函数体内。
      而且，这些内部函数可以访问它们所在的外部函数中声明的所有局部变量、参数和声明的其他内部函数。
      当其中一个这样的内部函数在包含它们的外部函数之外被调用时，就会形成闭包
    </p>

    <h2 class="color-green mt-20">闭包的作用</h2>
    <p>
      通过使用闭包,我们可以做很多事情。比如模拟面向对象的代码风格;
      更优雅，更简洁的表达出代码；在某些方面提升代码的执行效率
    </p>
    <p>
      一个是前面提到的可以读取函数内部的变量,另一个就是让这些变量的值始终保持在内存中
    </p>

    <h2 class="color-green mt-20">闭包的特点</h2>
    <p>
      1. 由于闭包会使得函数中的变量都被保存在内存中,内存消耗很大,所以不能滥用闭包,
      否则会造成网页的性能问题,在IE中可能导致内存泄露。
      解决方法是,在退出函数之前，将不使用的局部变量全部删除。
    </p>

    <h2 class="color-green mt-20">JS 垃圾回收机制</h2>
    <p>
      1. 如果一个对象不再被引用,那么这个对象就会被GC回收。<br/>
      2. 如果两个对象互相引用,而不再被第3者所引用,那么这两个互相引用的对象也会被回收。<br/>
      3. 因为函数a被b引用,b又被a外的c引用,这就是为什么函数a执行后不会被回收的原因
    </p>
  </div>
